import {AtomicDesign} from '../../../../../Frontend/Core/AtomicDesign';
import {EVENTS,REASONS} from '../../../../UiResources/events';
import {EDITABLE_TYPES} from '../../../../../Common/VariousTypes';
import Strings from '../../../../UiResources/strings';
import Blockly from 'blockly';

const blocklyDiv = "block-editor-organism__blockly";
let mainWorkSpace;

export default class BlockEditorOrganism extends AtomicDesign {

    workspace = null;
    hasData = false;

    editableLevelProperties = {
        api: null,
        allRequests: null,
        deployment: null,
        cron: null,
        module: null
    }

    // documentProperties = null;
    isWorkspaceLoading = false;
    toolbox = null;

    setWorkspaceProperties(){
        const editableType = this.vue.selectedItems?.editable?.type;
        let selectedBlock;
        switch(editableType){
            case EDITABLE_TYPES.DOCUMENT_LEVEL:
                selectedBlock = this.editableLevelProperties.allRequests;
                break;
            case EDITABLE_TYPES.API:
                selectedBlock = this.editableLevelProperties.api;
                break;                
            case EDITABLE_TYPES.ADMIN:
                selectedBlock = this.editableLevelProperties.deployment;
                break;
            case EDITABLE_TYPES.CRON:
                selectedBlock = this.editableLevelProperties.cron;
                break;
            case EDITABLE_TYPES.MODULE:
                selectedBlock = this.editableLevelProperties.module;
                break;
            default : 
                selectedBlock = null;
                break;
        }

        this.actions().setSelectedBlock(selectedBlock);
    }

    addEventHandlers(){
        this.onGlobalEvent(EVENTS.DOCUMENT.BEFORE_SAVE,this.beforeDocumentSave);
        this.onGlobalEvent(EVENTS.DOCUMENT.ON_PROPERTIES_CHANGED,this.propertiesChanged);
        this.onGlobalEvent(EVENTS.DOCUMENT.EDITABLE_SWITCHED,this.editableSwitched);
        this.onGlobalEvent(EVENTS.TOOLBAR.UNDO,this.undo);
        this.onGlobalEvent(EVENTS.TOOLBAR.REDO,this.redo);
    }
    
    undo(){
        this.workspace.undo(false);
    }

    redo(){
        this.workspace.undo(true);
    }

    editableSwitched(editable){
        switch(editable.type){
            case EDITABLE_TYPES.API:
            case EDITABLE_TYPES.DOCUMENT_LEVEL:
            case EDITABLE_TYPES.ADMIN:
            case EDITABLE_TYPES.CRON:
            case EDITABLE_TYPES.MODULE:
                this.updateWorkspaceFromSelectedEditable(editable);
                this.setWorkspaceProperties();
                break;
        }
    }

    propertiesChanged({editable}){
        this.updateSelectedEditable(editable);
    }

    beforeDocumentSave({reason}){
        const editable = this.vue.selectedItems?.editable;
        if (editable){
            if (reason !== REASONS.BEFORE_SAVE.USER_SAVE){
                const allBlocks = this.workspace.getAllBlocks();
                if (allBlocks.length !== 0){
                    const initialX = this.workspace.scrollX;
                    const initialY = this.workspace.scrollY;
                    this.workspace.centerOnBlock(allBlocks[0].id);
                    const diffX = this.workspace.scrollX - initialX;
                    const diffY = this.workspace.scrollY - initialY;
                    editable.scrollX = diffX;
                    editable.scrollY = diffY;
                    this.workspace.scrollX  =  diffX + initialX;
                    this.workspace.scrollY  =  diffY + initialY;
                }else {
                    editable.scrollX = this.workspace.scrollX;
                    editable.scrollY = this.workspace.scrollY;
                }
            }
          this.updateSelectedEditable(editable);
        }
    }

    createDocLevelBlocks(result, blocks){       
        blocks.forEach(block=>{
            const [blockName,propName] = block;
            if (!this.editableLevelProperties[propName]){
                const rootBlock = result.blocks.find(block => block.name == blockName);
                this.editableLevelProperties[propName] = {
                    id :0,
                    type:blockName,
                    properties: rootBlock.properties
                };
            }
        })

    }

    async getAssets(){
        const blockTypeId = "Api";
        const params = {staticCache:true,localStorage:true, busyTitle: Strings.PROGRESS_INDICATOR.LOADING_BLOCKS};
        const {result,error} = await this.backend(params).getBlocksAndToolbox(blockTypeId);
        
        if (!error){
            this.injectCustomBlocks(result.blocks);
            this.actions().patchSelectedItems({blocks:result.blocks});
            this.toolbox = result.toolbox;

            const docLevelBlocks = [
                ["$-api", "api"],
                ["$-admin", "deployment"],
                ["$-all", "allRequests"],
                ["$-cron", "cron"],
                ["$-module", "module"]
            ]

            const guideResposne = await this.backend(params).getGuides(blockTypeId);

            if (!guideResposne.error){
                this.actions().setGuides(guideResposne.result);
            }

            this.createDocLevelBlocks(result, docLevelBlocks);
            this.toolBoxLoaded();

        }else {
            this.msgbox(Strings.ERROR_MESSAGES.MESSAGE_API_BLOCKS, this.MSGBOX_TYPE.OK, Strings.ERROR_MESSAGES.TITLE_MESSAGE_API_BLOCKS);
        }
    }

    toolBoxLoaded(){
        this.initializeBlockly();
        this.updateWorkspaceFromSelectedEditable(this.vue.selectedItems.editable);
        this.setWorkspaceProperties();
    }

    injectCustomBlocks(blocks){
        for (const block of blocks){
            Blockly.Blocks[block.name] = (function(block){
                return {
                    init: function() {
                        this.jsonInit(block.data);
                    }
                };
            })(block);
        }
    }

    initializeBlockly(){
        var workspaceSettings = {
            toolbox: this.toolbox, 
            trashcan: false,
            sounds:	false,
            zoom: true,
            theme: Blockly.Themes.Dark,
            grid:{
                spacing: 10,
                length: 10,
                colour: 'rgb(42, 42, 42)'
            }
        };

        Blockly.Scrollbar.scrollbarThickness = 15;
        this.workspace = Blockly.inject(blocklyDiv,workspaceSettings);
        mainWorkSpace = this.workspace;
        this.workspace.addChangeListener(Blockly.Events.disableOrphans);
        this.workspace.addChangeListener((changeEvent) => {

            if (changeEvent.type == "ui"){
                if (changeEvent.element == "click")
                {
                    const block = this.workspace.getBlockById(changeEvent.blockId);
                    //block.setFieldValue("TEST", "CAPTION");
                    this.actions().setSelectedBlock({id: block.id, type: block.type});
                }
    
                if (changeEvent.element == "selected"){
                    if (!Blockly.selected){
                        this.setWorkspaceProperties();
                    }
                }
            }
            
            if (changeEvent.type == "create"){
                if (Blockly?.selected?.id){
                    this.actions().setSelectedBlock({id: Blockly.selected.id, type: Blockly.selected.type});
                }
            }

            if (changeEvent.type == "delete"){
                if (!this.isWorkspaceLoading){
                    delete this.vue.selectedItems.editable.properties[changeEvent.blockId];
                }
            }

            if (changeEvent.type == "finished_loading"){
                this.isWorkspaceLoading = false;
            }           
            
        });

        document.getElementsByClassName("blocklyMainBackground")[0].style.stroke = "none";  

    }


    updateWorkspaceFromSelectedEditable(editable){
        if (!editable){
            return;
        }
        this.isWorkspaceLoading = true;

        if (this.workspace == null){   
            this.initializeBlockly();
        }
        //console.log(this.workspace.scrollbar);
        
        this.workspace.clear();
        // this.actions().setExecutionFlow(null);
        //this.actions().setCompileErrors(null);

        const wfObj  = editable.workflows;
        const xmlText = wfObj ? wfObj[this.vue.selectedWorkflow] : null;

        if (xmlText){
            this.hasData = true;
            const xml = Blockly.Xml.textToDom(xmlText);
            Blockly.Xml.domToWorkspace(xml, this.workspace);
            
           
            const allBlocks = this.workspace.getAllBlocks();
            
            if (editable.scrollX && editable.scrollY){
                if (allBlocks.length !== 0){
                    this.workspace.centerOnBlock(allBlocks[0].id);
                    this.workspace.scrollX = this.workspace.scrollX - editable.scrollX;
                    this.workspace.scrollY = this.workspace.scrollY - editable.scrollY;
                }else {
                    this.workspace.scrollX = editable.scrollX;
                    this.workspace.scrollY = editable.scrollY;
                }
                this.workspace.resize();
    
            }else {
                if (allBlocks.length !== 0){
                    this.workspace.centerOnBlock(allBlocks[0].id);
                }
            }
           
            this.workspace.clearUndo();
            
        }
        else {
            this.hasData = false;
        }
    }

    updateSelectedEditable(editable){
        if (editable){
            if (editable.type == EDITABLE_TYPES.API || EDITABLE_TYPES.DOCUMENT_LEVEL){
                let editableObj = this.vue.openedDocument.editables.filter(e=>e.id == editable.id)[0];
                if (editableObj){
                    if (!editableObj.workflows){
                        editableObj.workflows = {};
                    }
            
                    const selectedWfName = this.vue.selectedWorkflow;
                    const xml = Blockly.Xml.workspaceToDom(this.workspace);
                    const xmlText = Blockly.Xml.domToText(xml);
                    editableObj.workflows[selectedWfName] = xmlText;

                    // const allBlocks = this.workspace.getAllBlocks();
                    // if (allBlocks.length !== 0){
                    //     const initialX = this.workspace.scrollX;
                    //     const initialY = this.workspace.scrollY;
                    //     this.workspace.centerOnBlock(allBlocks[0].id);
                    //     const diffX = this.workspace.scrollX - initialX;
                    //     const diffY = this.workspace.scrollY - initialY;
                    //     editableObj.scrollX = diffX;
                    //     editableObj.scrollY = diffY;
                    //     this.workspace.scrollX  =  diffX + initialX;
                    //     this.workspace.scrollY  =  diffY + initialY;
                    // }else {
                    //     editableObj.scrollX = this.workspace.scrollX;
                    //     editableObj.scrollY = this.workspace.scrollY;
                    // }
            
                    this.actions().setOpenedDocument({result:this.vue.openedDocument});
                }
            }
        }

    }

    resizeWorkspace(){
        const panels = this.panels;
        const rightPanel = panels.right;
        const bottomPanel = this.compileErrors;
  
        let widthDeduction = 661;
        let heightDeduction = 61;
        
        if (!rightPanel){
          widthDeduction -= 300;
        }
        
        if (bottomPanel){
            heightDeduction += 150;
        }

        const size = {
          width: `calc(100% - ${widthDeduction}px)`,
          height: `calc(100% - ${heightDeduction}px)`,
        }
  
        if (mainWorkSpace){
            const element = document.getElementById(blocklyDiv);
            if (element){
                element.style.width = size.width;
                element.style.height = size.height;
                Blockly.svgResize(mainWorkSpace);
            }
        }
        
        return size;
    }

    resetStore(){
        this.actions().setSelectedBlock(null);
        this.actions().setGuides(null);
    }

    resetWorkspace() {
        // this.workspace = null;
        //mainWorkSpace = null;

        this.editableLevelProperties = {
            api: null,
            allRequests: null,
            deployment: null,
            cron: null,
            module: null
        };

        this.hasData = false;
        this.isWorkspaceLoading = false;
    }
}