<template>
  <div class="block-editor-organism">
    <div id="block-editor-organism__blockly" class="block-editor-organism__blockly-workspace" :style="size"></div>
  </div>
</template>

<script>
import BlockEditorOrganism from './BlockEditorOrganism.hook'
const hook = new BlockEditorOrganism();
hook.setBackendNamespace("Pages/Editor/EditorPage").setElementName("BlockEditorOrganism");

export default {
  name: 'BlockEditorOrganism',
  beforeCreate() { hook.hook(this); hook.addEventHandlers();},
  methods: {},
  mounted() { 
    hook.getAssets(); 
  },
  beforeDestroy() { 
    const editable = this.selectedItems?.editable;
    if (editable){
      hook.updateSelectedEditable(editable);
    }
    hook.unsubscribeGlobalEvents();
    hook.resetStore();
    hook.resetWorkspace();
  },
  data() {
      return { 
        selectedWorkflow: "main"
      }
  },
  computed: {
    panels(){
      return this.openedDocument.selectedResources.panels;
    }, 
    size: hook.resizeWorkspace,
    ...hook.getters("selectedItems","openedDocument","selectedBlock","compileErrors")
  }
  
}

</script>

<style scoped lang="scss">

.blocklyMainBackground {
  stroke: none !important;
}

.block-editor-organism {
    height:calc(100% - 150px);
    &__blockly-workspace {
      position: absolute;
      overflow: hidden;
      border: none;
  }
}


</style>
