import {AtomicDesign} from '../../../../Frontend/Core/AtomicDesign';
import {getTopToolbarButtons} from '../../../../Frontend/Helpers/toolbar';
import {EVENTS,REASONS} from '../../../UiResources/events';

export default class EditorPage extends AtomicDesign {

    addEventHandlers(){
        this.onGlobalEvent(EVENTS.RUN.SUCCESSFUL, this.runSuccessful);
    }

    clearResources(){
        this.vue.selectedRightTab = "0";
    }

    runSuccessful(){
        this.vue.selectedRightTab = "1";
    }

    initToolbar(){
        const toolbarButtons = getTopToolbarButtons("NEW_ITEM","SAVE",
        "UNDO","REDO","CUT","COPY","PASTE",
        "FULLSCREEN","RUN","RUN_DEFAULT","VIEW_DEFAULT","VIEW_DETAILED",
        // "NEW_FOLDER"
        );
        this.actions().setTopToolbar(toolbarButtons);
    }

    closeRightPanel(){

    }
    
    async confirmLeave(to,from,next){
        const result = await this.msgbox("Do you want to save your changes before leaving?", this.MSGBOX_TYPE.YES_NO_CANCEL, "Confirm save");
        switch (result){
            case this.MSGBOX_BUTTONS.YES:
                this.emitGlobalEvent(EVENTS.DOCUMENT.BEFORE_SAVE,{reason:REASONS.BEFORE_SAVE.USER_LEAVE});
                this.emitGlobalEvent(EVENTS.TOOLBAR.SAVE, {callback:()=>{
                    this.actions().setExecutionFlow(null);
                    this.actions().setCompileErrors(null);
                    next();
                }});
                break;
            case this.MSGBOX_BUTTONS.NO:
                this.actions().setExecutionFlow(null);
                this.actions().setCompileErrors(null);
                next();
                break;
            case this.MSGBOX_BUTTONS.CANCEL:
                next(false);
                break;
        }
    }


}