<template>
  <div class="editor-page">
    <div class="editor-page__events" v-if="panelSettings.left">
      <DefaultViewOrganism v-if="openedDocument.selectedResources.defaultView == 'default'"/>
      <DetailedViewMolecule v-if="openedDocument.selectedResources.defaultView == 'detailed'"/>
    </div>
    <div class="editor-page__container" :style="{width: size.width}">
      
      <BlockEditorOrganism v-if="
      editableType == EDITABLE_TYPES.API || 
      editableType == EDITABLE_TYPES.DOCUMENT_LEVEL || 
      editableType == EDITABLE_TYPES.ADMIN || 
      editableType == EDITABLE_TYPES.CRON ||
      editableType == EDITABLE_TYPES.MODULE"/>
      
      <ScriptEditorOrganism v-if="
      editableType == EDITABLE_TYPES.SCRIPT ||
      editableType == EDITABLE_TYPES.CSV ||
      editableType == EDITABLE_TYPES.TXT ||
      editableType == EDITABLE_TYPES.JSON ||
      editableType == EDITABLE_TYPES.HTML ||
      editableType == EDITABLE_TYPES.SQL" />
      
      <SchemaEditorOrganism v-if="editableType == EDITABLE_TYPES.SCHEMA" />   
      
      <div class="editor-page__container__errors" v-if="compileErrors">
        <CompileErrorsMolecule/>
      </div>

    </div>


    <div class="editor-page__props" v-if="panelSettings.right">
      <!-- <div class="editor-page__props__close">
        <i @click="panelSettings.right = false" class="el-icon-close"></i>
      </div> -->

      <el-tabs type="card" v-model="selectedRightTab">
        <el-tab-pane label="Properties">
          <PropertiesOrganism/>
        </el-tab-pane>
        <el-tab-pane label="Execution">
          <ExecutionFlowOrganism/>
        </el-tab-pane>
        <el-tab-pane label="Guide">
          <GuidesMolecule/>
        </el-tab-pane>
      </el-tabs>
    </div>

    <!-- <div class="editor-page__right-stripe" v-if="!panelSettings.right">
      <i @click="panelSettings.right = true" class="el-icon-caret-left"></i>
    </div> -->
  </div>
</template>

<script>
import DetailedViewMolecule from './DetailedViewMolecule/DetailedViewMolecule';
import DefaultViewOrganism from './DefaultViewOrganism/DefaultViewOrganism';
import BlockEditorOrganism from './BlockEditorOrganism/BlockEditorOrganism';
import ScriptEditorOrganism from './ScriptEditorOrganism/ScriptEditorOrganism';
import SchemaEditorOrganism from './SchemaEditorOrganism/SchemaEditorOrganism';
import PropertiesOrganism from './PropertiesOrganism/PropertiesOrganism';
import GuidesMolecule from './GuidesMolecule/GuidesMolecule';
import ExecutionFlowOrganism from './ExecutionFlowOrganism/ExecutionFlowOrganism';
import CompileErrorsMolecule from './CompileErrorsMolecule/CompileErrorsMolecule';
import {EDITABLE_TYPES} from '../../../../Common/VariousTypes';

import EditorPage from './EditorPage.hook';
const hook = new EditorPage();

export default {
  name: 'EditorPage',
  beforeCreate() { hook.hook(this); hook.addEventHandlers();},
  mounted(){ hook.initToolbar(); },
  beforeDestroy(){ hook.clearToolBar(); hook.unsubscribeGlobalEvents(); hook.clearResources(); }, 
  beforeRouteLeave: async function (to, from, next) {
    if (to.path === "/"){
      hook.confirmLeave(to,from,next);
    }else {
      next();
    }
    
  },
  methods: hook.methods("closeRightPanel"),
  components: { 
    DefaultViewOrganism,
    DetailedViewMolecule,
    BlockEditorOrganism,
    ScriptEditorOrganism,
    SchemaEditorOrganism,
    PropertiesOrganism,
    GuidesMolecule,
    ExecutionFlowOrganism,
    CompileErrorsMolecule
  },
  data() {
    return {
      EDITABLE_TYPES,
      selectedRightTab: "0"
    }
  },
  computed: {
    showExecutionFlow(){
      return this.executionFlow;
    },
    editableType(){
      return this.selectedItems?.editable?.type || 1;
    },
    panelSettings(){
      return this.openedDocument.selectedResources.panels;
    },
    panels(){
      return this.openedDocument.selectedResources.panels;
    }, 
    size(){
        const panels = this.panels;
        const rightPanel = panels.right;
        const bottomPanel = panels.right;
  
        let widthDeduction = 602;
        let heightDeduction = 61;

        if (!rightPanel){
          widthDeduction -= 300;
        }
        
        if (bottomPanel) {
          heightDeduction += 150;
        }

        const size = {
          width: `calc(100% - ${widthDeduction}px)`,
          height: `calc(100% - ${heightDeduction}px)`,
        }
         
        return size;
    },
    ...hook.getters("openedDocument","selectedItems","selectedBlock","executionFlow","compileErrors")
  }
};

</script>

<style lang="scss">
.editor-page{
  display: flex;
  height:100%;
  width:100%;
  
  &__events{
    padding-top:20px;
    width:300px;
    background-color: #1E1E1E;
  }

  &__container{
    display: flex;
    flex-direction: column;
    &__errors {
      height:150px;
    }
  }

  &__right-stripe{
    position: fixed;
    width:20px;
    right:20px;
    background-color: #333333;
  }

  &__props{
    width: 300px;
    background-color: #333333;

    &__close{
      width:100%;
      background-color: #1E1E1E;
      height:20px;
    
      text-align: right;
      i:hover {
        opacity: 0.5;
      }

      i {
        cursor: pointer;
        font-size: 20px;
        transition: 0.3s;
      }
    }

  }
}
</style>
